import * as Chartist from 'chartist';
import { BaseChart } from '@common/shared/charts/base-chart';
export class ChartistPie extends BaseChart {
    constructor() {
        super(...arguments);
        this.pieConfig = {
            showLabel: false,
            chartPadding: 0
        };
    }
    generate() {
        this.chart = new Chartist.Pie(this.config.selector, this.transformChartData(), this.pieConfig);
    }
    transformChartData() {
        return {
            labels: this.config.labels,
            series: this.config.data,
        };
    }
    destroy() {
        if (this.chart) {
            this.chart.detach();
        }
    }
}
